.projects {
  font-size: 2.5rem;
  color: #153f59;
  margin: 0px 0px 0px 20px;
  display: inline;
}

.main_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px auto 150px auto;
}

.title_container {
  margin-bottom: 50px;
}

.etiquetaP {
  color: #a61f2b;
  font-weight: 600;
  font-size: 1.2rem;
  font-family: "Courier New", Courier, monospace;
  margin: 10px 0;
}

.image {
  display: inline;
  width: 130px;
  height: 10px;
  margin-left: 10px;
  vertical-align: middle;
}

.grid {
  max-width: 1000px;
}

@media (max-width: 434px) {
  .projects {
    margin: 0;
  }
}
